.App {
  width: auto;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.quadrant-left{

  border-color: #282c34;
  height: 100%;
  padding: 20% 15% 20% 0%;
}

.quadrant-right{

  border-color: #282c34;
  height: 100%;
  padding: 20% 0% 20% 15%;
}
.TopBox{
  padding: 10% 0;
}

.BottomBox{
  padding: 10% 0;
}
